			}
		}
	}
</script>

<style type="text/css" lang="less" scoped>
	.test{
		width: 100%;
		display: flex;
		justify-content: center;
		.content{
			width:1000px;
			padding:0 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			padding-top:80px;
			padding-bottom: 80px;
			.movie{
				display: flex;
				flex-direction: column;
				align-items: center;
				padding-bottom: 50px;
				h1{
					display: flex;
					align-items: center;
					>span{
						margin-right:20px;
					}
				}
			}
			.next{
				border:1px solid #d7d7d7;
				padding:10px 20px;
				border-radius: 10px;
				cursor: pointer;
				background-color:#081c24;
				color:rgba(255,255,255,0.9);
				.small{
					font-size:10px;
				}
			}
		}
	}
	@media screen and (max-width: 768px) {
		.test{
			.content{
				width:100%;
				padding:0 10px;
				box-sizing: border-box;
				padding-bottom: 30px;
				.movie{
					>img{
						width:300px;
					}
					h1{
						font-size: 20px;
					}
					p{
						font-size: 16px;
					}
				}
			}
		}
	}
</style>


